import React from 'react';

import {
	Button,
	FormControl,
	FormControlLabel,
	Grid,
	Radio,
	RadioGroup,
} from '@material-ui/core';

import LightInput from '../components/LightInput';
import DarkInput from '../components/DarkInput';

import { ThemeProvider } from '@material-ui/styles';
import { createMuiTheme } from '@material-ui/core/styles';

import FormatQuoteRoundedIcon from '@material-ui/icons/FormatQuoteRounded';
import NavigateBeforeRoundedIcon from '@material-ui/icons/NavigateBeforeRounded';
import NavigateNextRoundedIcon from '@material-ui/icons/NavigateNextRounded';

const lightTheme = createMuiTheme({
	palette: {
		type: 'light',
	},
});

const isMobile = /Android|webOS|iPhone|iPad|IEMobile|Opera Mini/i.test(
	navigator.userAgent
);
const size = isMobile ? 'small' : 'medium';

const style = isMobile ? null : { margin: '0 2em 0 2em' };

function Seventh({ handleChoice, onSubmit, value, goBack }) {
	return (
		<div className="question-body">
			<h1>
				<FormatQuoteRoundedIcon />
				Now, take a look at these input field and tell us which one
				you'd prefer
			</h1>
			<p>
				Feel free to play around with the elements, it won't impact your
				overall survey answer
			</p>
			<ThemeProvider theme={lightTheme}>
				<form onSubmit={onSubmit}>
					<FormControl component="fieldset">
						<Grid container spacing={1}>
							<RadioGroup
								aria-label="textField"
								name="textField"
								value={value}
								onChange={handleChoice}
								row={!isMobile}
								style={{
									margin: '1em 0 2em 0',
								}}
							>
								<div className="column" style={style}>
									<div className="dark">
										<DarkInput />
									</div>
									<FormControlLabel
										value="darkMode"
										style={{ margin: '1em 0 0 0' }}
										control={<Radio color="primary" />}
									/>
								</div>
								<div className="column" style={style}>
									<div className="light">
										<LightInput />
									</div>
									<FormControlLabel
										value="lightMode"
										style={{ margin: '1em 0 0 0' }}
										control={<Radio color="primary" />}
									/>
								</div>
							</RadioGroup>
						</Grid>
						<div className="btn-group">
							<Button
								color="default"
								variant="contained"
								startIcon={
									isMobile ? null : (
										<NavigateBeforeRoundedIcon />
									)
								}
								onClick={goBack}
								style={{ margin: '0 1em 0 auto' }}
								size={size}
							>
								Take me back
							</Button>
							<Button
								type="submit"
								color="primary"
								variant="contained"
								endIcon={
									isMobile ? null : (
										<NavigateNextRoundedIcon />
									)
								}
								style={{ margin: '0 auto 0 1em' }}
								disabled={value === ''}
								size={size}
							>
								Next question
							</Button>
						</div>
					</FormControl>
				</form>
			</ThemeProvider>
		</div>
	);
}

export default Seventh;
